<template>
    <div class="content-bg"  v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
      <div class="top-title">应用性能分析</div>
      <div style="padding: 0 5px;"><v-basedate class="from-wapper" busName="performanceAnalysis"></v-basedate></div>
      <div class="item-wapper">
        <div class="item-title">{{this.chartTitle}}<span v-if="this.chartDataType !== 'all'" class="goBack" @click="goBack">返回</span></div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.appAvgResTimeData" :busName="this.busName"></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
  import vBasedate from '../common/baseDate'
  import vEcharts from '../common/echarts'
  import bus from '../common/bus';
	export default {
		name: "performanceAnalysis",
		data() {
			return {
        fullscreenLoading:false,
        //图表类型
        chartDataType:'all',
        chartTitle:'应用平均响应时间统计',
        allParams:{},
        oneParams:{},
        clickxAxis:'',
        busName:{
          exportName:'',
          clickName:'appAvgResTimeClick'
        },
        //应用平均响应时间统计数据
        appAvgResTimeData:{
          baseConfig:{
            title:'',
            xAxisName:'应用\n名称',
            yAxisName:'时间/ms',
            rotate:'15',
            dispose:true,
            itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
          },
          xAxisArr:[],
          yAxisArr:[]
        },
        appAvgResTimeData2:{
          baseConfig:{
            title:'',
            hoverText:'百分比'
          },
          xAxisArr:[],
          yAxisArr:[]
        }
      }
		},
    created(){
      /*监听刷新时间改变*/
      bus.$on('performanceAnalysis',(arr)=> {
        //判断图表数据类型
        if(this.chartDataType === 'all'){
          let paramObj = {
            starttime:arr[0],
            endtime:arr[1]
          }
          this.fullscreenLoading = true;
          /*获取应用平均响应时间数据*/
          this.getAppAvgResTimeData(paramObj);
        }else{
          let obj = {
            domain_url:this.clickxAxis,
            starttime:arr[0],
            endtime:arr[1]
          }
          this.fullscreenLoading = true;
          this.getOneAppfuncResTimeData(obj);
        }
      })
      /*监听点击事件*/
      bus.$on('appAvgResTimeClick',(data)=>{
        //保存点击的x轴
        this.clickxAxis = data.name;
        let obj = {
          starttime:this.allParams.starttime,
          endtime:this.allParams.endtime,
          domain_url:data.name
        }
        console.log(data.name)
        this.getOneAppfuncResTimeData(obj);
      })
    },
    methods:{
      /*获取应用平均响应时间数据*/
      getAppAvgResTimeData(obj){
        let paramObj = JSON.stringify(obj)
        this.chartDataType = 'all'
        this.allParams = obj;
        this.chartTitle = "应用平均响应时间统计"
        this.busName.clickName = 'appAvgResTimeClick';
        this.$nextTick(()=>{
          this.$post('/flow/getDomaiUrlAvgResponsetime.do',{
            hsData:paramObj
          })
            .then(res=>{
              this.fullscreenLoading = false;
              let xns1 = [];
              let yvs1 = [];
              let yvs2 = [];
              for(let i in res){
                let obj = res[i];
                xns1.push(obj.key);
                yvs1.push(obj.value.toFixed(2))
                /*yvs2.push({
                    name:j,
                    value:obj[j]
                })*/
              }
              this.appAvgResTimeData.xAxisArr = xns1;
              this.appAvgResTimeData.yAxisArr = yvs1;
              //this.ipPacketsData2.yAxisArr = yvs2;
            })
            .catch(err=>{
              this.fullscreenLoading = false;
            })
        })
      },
      /*获取单个应用功能URL平均响应时间*/
      getOneAppfuncResTimeData(obj){
        let paramObj = JSON.stringify(obj)
        this.chartDataType = 'one'
        this.chartTitle = this.clickxAxis + " 功能URL平均响应时间统计";
        this.busName.clickName = '';
        this.$nextTick(()=>{
          this.$post('/flow/getRequestUrlAvgResponsetime.do',{
            hsData:paramObj
          })
            .then(res=>{
              this.fullscreenLoading = false;
              let xns1 = [];
              let yvs1 = [];
              let yvs2 = [];
              for(let i in res){
                let obj = res[i];
                xns1.push(obj.key);
                yvs1.push(obj.value.toFixed(2))
                /*yvs2.push({
                    name:j,
                    value:obj[j]
                })*/
              }
              this.appAvgResTimeData.xAxisArr = xns1;
              this.appAvgResTimeData.yAxisArr = yvs1;
              //this.ipPacketsData2.yAxisArr = yvs2;
            })
            .catch(err=>{
              this.fullscreenLoading = false;
            })
        })
      },
      /*返回*/
      goBack(){
        this.chartTitle = "应用平均响应时间统计"
        this.fullscreenLoading = true;
        /*获取应用平均响应时间数据*/
        this.getAppAvgResTimeData(this.allParams);
      }
    },
    components:{
      vBasedate,
      vEcharts
    }
	}
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
  .goBack{
    float: right;
    margin-right: 20px;
    color: #15c5e3;
    cursor: pointer;
    font-size: 14px;
  }
</style>



